export default {
	data() {
		return {
			type: 1,
			newsList: [],
			pageSize: 20,
			pageIndex: 1,
			pageTotal: 1,
			nodata: false,
			nomore: false
		};
	},
	onLoad(options) {
		this.type = options.type
		let title = ''
		if (this.type == 1) {
			title = '系统消息'
		} else if (this.type == 2) {
			title = '文章消息'
		} else if (this.type == 3) {
			title = '订单消息'
		}
		uni.setNavigationBarTitle({
			title: title
		})
	},
	onShow() {
		this.setParams()
		this.getNewsList()
	},
	onPullDownRefresh() {
		this.setParams()
		this.getNewsList()
		uni.stopPullDownRefresh()
	},
	onReachBottom() {
		if (this.pageIndex < this.pageTotal) {
			this.pageIndex++
			this.getNewsList()
		} else {
			this.nomore = true
		}
	},
	methods: {
		setParams() {
			this.pageIndex = 1
			this.nomore = false
			this.nodata = false
			this.newsList = []
		},
		getNewsList() {
			let self = this
			let params = {
				fromType: this.type,
				pageIndex: this.pageIndex,
				pageSize: this.pageSize
			}
			this.$acFrame.HttpService.newsList(params).then(res => {
				if (res.success) {
					let list = res.data.rows
					self.pageTotal = res.data.pageTotal
					if (list.length > 0) {
						list.filter(v => {
							v.createTime = self.$acFrame.Util.formatTime(v.createTime, 'day')
							if (v.fromType == 2) {
								switch (v.type) {
									case 1:
										v.newsMsg = '关注'
										break;
									case 2:
										v.newsMsg = '取消关注'
										break;
									case 3:
										v.newsMsg = '点赞'
										break;
									case 4:
										v.newsMsg = '取消点赞'
										break;
									case 5:
										v.newsMsg = '点赞评论'
										break;
									case 6:
										v.newsMsg = '取消点赞评论'
										break;
									case 7:
										v.newsMsg = '转发分享'
										break;
									case 8:
										v.newsMsg = '评论'
										break;
									case 9:
										v.newsMsg = '回复评论'
										break;
									default:
										v.newsMsg = ''
										break;
								}
							}

						})
						console.log(list)
						self.newsList = self.newsList.concat(list)
					} else {
						self.nodata = true
					}
				}
			})
		},
		setImg(src) {
			if (!src) {
				src = ''
			}
			return this.$acFrame.Util.setImgUrl(src);
		},
		readyNews(item) {
			let params = {
				fromType: item.fromType,
				id: item.id
			}
			this.$acFrame.HttpService.readNews(params).then(res => {
				if (res.success) {}
			})
		},
		mycenter(item) {
			this.readyNews(item)
			uni.navigateTo({
				url: 'mycenter?userCode=' + item.userInfo.userCode
			})
		},
		articlDetail(item) {
			return false;
			this.readyNews(item)
			uni.navigateTo({
				url: "/pages/home/commentDetail?articalId=" + id
			})
		}
	}
}
